(function () {
    'use strict';

    angular.module('dctv-motor-busca-carro.directive', ['dctv-motor-busca-carro.factory'])
        .directive('dctvMotorBuscaCarro', dctvMotorBuscaCarro);

    /** @ngInject */
    function dctvMotorBuscaCarro() {

        return {
            restrict: 'E',
            replace: true,
            scope: {
                aoRetornarBusca: '='
            },
            controllerAs: 'controller',
            templateUrl: 'app/common/directive/dctv-motor-busca-carro/dctv-motor-busca-carro.tpl.html',
            link: function (scope, element, attrs) {

                scope.buscar = buscar;
                scope.devolverEmOutroLocal = true;

                var _valoresTituloAnuncio = ['Carro Econômico com AR', 'Carro Econômico sem AR'];
                var _valoresDescricao = ['Chevrolet Celta, Ford Fiesta, VW Gol, Fiat Pálio ou Similar', 'Chevrolet Captiva, Ford Fusion, VW Passat, Fiat Bravo ou Similar'];
                var _valoresDetalhes = [];
                var _valoresCodigoTaxa = ['IFMR'];
                var _valoresCaracteristicas = ['Quilometragem Livre', 'Seguro total do veículo', 'Seguro a terceiros', 'Taxas de serviços inclusas'];
                var _valoresImagem = ['assets/images/gol-g5.jpg', 'assets/images/sandero.jpg'];

                buscar();

                function obterIndiceRandomico(vetor) {
                    return vetor[obterNumeroRandomico(vetor.length) - 1];
                }

                function obterNumeroRandomico(maximo) {
                    return Math.floor((Math.random() * maximo) + 1);
                }

                function buscar(modelo) {

                    var _retorno = [];
                    for (var x = 0; x < obterNumeroRandomico(600); x++) {
                        var objeto = {
                            tituloAnuncio: obterIndiceRandomico(_valoresTituloAnuncio),
                            descricao: obterIndiceRandomico(_valoresDescricao),
                            detalhes: 'Super inclusive promocional - Km livre, seguro total do veículo (LDW), seguro a terceiros (SLI), taxas de serviços inclusas e 1 motorista adicional.',
                            imagem: obterIndiceRandomico(_valoresImagem),
                            codigoTaxa: obterIndiceRandomico(_valoresCodigoTaxa),
                            caracteristicasFisicas: [
                                {titulo: 'Portas', valor: 4},
                                {titulo: 'Pessoas', valor: obterNumeroRandomico(5)},
                                {titulo: 'Malas', valor: obterNumeroRandomico(5)},
                                {titulo: 'Ar Condicionado', valor: obterNumeroRandomico(2) == 2},
                                {titulo: 'Câmbio Automático', valor: obterNumeroRandomico(2) == 2},
                            ],
                            preco: parseFloat(obterNumeroRandomico(1000) + '.' + obterNumeroRandomico(99)),
                            detalhesPagamento: {
                                numeroParcelas: 10,
                                desconto: obterNumeroRandomico(2) == 2 ? obterNumeroRandomico(30) : undefined
                            }
                        };
                        objeto.caracteristicas = [];
                        for (var x = 0; x < obterNumeroRandomico(99); x++) {
                            var caracteristica = obterIndiceRandomico(_valoresCaracteristicas);
                            if (objeto.caracteristicas.indexOf(caracteristica) == -1) {
                                objeto.caracteristicas.push(caracteristica);
                            }
                        }
                        _retorno.push(objeto);
                    }
                    scope.aoRetornarBusca(_retorno);
                }

            }
        }

    }


})
();

(function () {
    'use strict';

    angular.module('dctv-lista-combo.directive', [])
        .directive('dctvListaCombo', dctvListaCombo);

    /** @ngInject */
    function dctvListaCombo(lodash, $filter) {

        return {
            restrict: 'E',
            replace: true,
            scope: {
                combos: '='
            },
            controllerAs: 'controller',
            templateUrl: 'app/common/directive/dctv-lista-combo/dctv-lista-combo.tpl.html',
            link: function (scope, element, attrs) {

                scope.obterCombos = obterCombos;
                scope.obterComboValores = obterComboValores;
                scope.obterComboExibicaoValor = obterComboExibicaoValor;
                scope.eComboCarregando = eComboCarregando;
                scope.obterAoSelecionar = obterAoSelecionar;

                scope.$watchCollection(function () {
                    return obterCombos();
                }, function () {
                    angular.element(obterCombos()).each(function (iC, combo) {
                        if (combo.carregar && combo.buscarValores) {
                            buscarComboValores(combo);
                        }
                    });
                });

                function obterCombos() {
                    return scope.combos;
                };

                function obterComboValores(combo) {
                    return combo.valores;
                }

                function obterComboExibicaoValor(combo, valor) {
                    if (angular.isFunction(combo.valorExibicao)) {
                        return combo.valorExibicao(valor);
                    }
                    else {
                        return valor[combo.valorExibicao];
                    }
                };

                function obterAoSelecionar(combo) {
                    return combo.aoSelecionar ? combo.aoSelecionar : angular.noop;
                };

                function eComboCarregando(combo) {

                };

                function buscarComboValores(combo) {
                    combo.buscarValores().then(sucesso, erro);
                    function sucesso(sucesso) {
                        combo.valores = [];
                        for (var key in sucesso.valores) {
                            sucesso.valores[key].simbolo = key;
                            combo.valores.push(sucesso.valores[key]);
                        }
                        combo.carregar = false;
                    };

                    function erro(erro) {
                        combo.carregar = false;
                    };
                };

            }
        }

    }


})
();

(function () {
    'use strict';

    angular
        .module('dctv.geo-codificador', [])
        .directive('dctvGeoCodificador', dctvGeoCodificador);

    /** @ngInject */
    function dctvGeoCodificador($http) {
        return {
            restrict: 'E',
            replace: true,
            scope: {
                callback: '='
            },
            controllerAs: 'controller',
            templateUrl: 'app/common/directive/dctv-geo-codificador/dctv-geo-codificador.tpl.html',
            link: function (scope, element) {

                scope.crud = {};
                scope.command = {};
                scope.source = {};
                scope.interface = {};
                scope.interface.opened = false;

                scope.command.BuscarEnderecos = function (endereco) {
                    var params = {address: endereco, sensor: false};
                    return $http.get(
                        'http://maps.googleapis.com/maps/api/geocode/json',
                        {params: params}
                    ).then(function (response) {
                        scope.source.enderecos = response.data.results;
                    });
                };

                scope.command.EnderecoSelecionado = function (endereco) {
                    scope.callback(endereco);
                };


            }
        }
    }

})();
(function () {
    'use strict';

    angular.module('dctv-filtro.directive', [])
        .directive('dctvFiltro', dctvFiltro);

    /** @ngInject */
    function dctvFiltro(lodash) {

        return {
            restrict: 'E',
            replace: true,
            scope: {
                lista: '=',
                listaProcessada:'=',
                filtros: '=',
                atributosFiltroString: '=',
                aoProcessarLista: '=',
                spanControladores: '=',
            },
            controllerAs: 'controller',
            templateUrl: 'app/common/directive/dctv-filtro/dctv-filtro.tpl.html',
            link: function (scope, element, attrs) {

                scope.obterFiltros = obterFiltros;
                scope.obterValoresFiltro = obterValoresFiltro;
                scope.obterGridSpan = obterGridSpan;
                scope.processarListaTexto = processarListaTexto;
                scope.processarListaFiltros = processarListaFiltros;

                scope.$watchCollection(function () {
                    return obterLista();
                }, function () {
                    obterLista().length > 0 ? processarListaFiltros() : processarListaTexto(obterTextoFiltro());
                });

                function eListaDeString(lista) {
                    return angular.isString(lista[0]);
                };

                function obterFiltros() {
                    return scope.filtros ? scope.filtros : [];
                };
                function obterValoresFiltro(filtro) {
                    var retorno = [];
                    angular.element(obterLista()).each(function (iI, item) {
                        if (angular.isArray(item[filtro.propriedade])) {
                            retorno.push.apply(retorno, lodash.filter(item[filtro.propriedade], function (valores) {
                                return retorno.indexOf(valores) == -1;
                            }));
                        }
                        else if (retorno.indexOf(item[filtro.propriedade]) == -1) {
                            retorno.push(item[filtro.propriedade]);
                        }
                    });
                    return retorno;
                };
                function obterLista() {
                    return scope.lista ? scope.lista : [];
                };
                function obterGridSpan() {
                    var propriedade = 'spanControladores';
                    return angular.isUndefined(scope[propriedade]) ? obterDefaults(propriedade) : scope[propriedade];
                };
                function obterDefaults(valor) {
                    var _defaults = {
                        spanControladores: 6
                    };
                    return _defaults[valor];
                };
                function obterAtributosFiltroString() {
                    return scope.atributosFiltroString ? scope.atributosFiltroString : [];
                };
                function obterTextoFiltro() {
                    return scope.textoFiltro;
                };

                function processarListaTexto(valorFiltroString) {
                    if (valorFiltroString) {
                        if (eListaDeString(obterLista())) {
                            scope.aoProcessarLista(filtrarListaString(obterLista(), valorFiltroString));
                        }
                        else {
                            scope.aoProcessarLista(filtrarListaObjeto(obterLista(), valorFiltroString, obterAtributosFiltroString()));
                        }
                    }
                    else {
                        scope.aoProcessarLista(obterLista());
                    }
                };

                function processarListaFiltros(filtro) {
                    scope.aoProcessarLista(filtrarListaPorFiltros(obterLista(), obterFiltros()));
                };

                function filtrarListaString(vetor, valor) {
                    return lodash.filter(vetor, function (item) {
                        return item && item.toString().toLowerCase().indexOf(valor.toString().toLowerCase()) > -1;
                    });
                };
                function filtrarListaObjeto(vetor, valor, filtros) {
                    var retorno = [];
                    filtros = filtros.length > 0 ? filtros : Object.keys(vetor[0]);
                    angular.element(filtros).each(function (iF, filtro) {
                        var _filtrados = lodash.filter(vetor, function (item) {
                            var _valorPropriedade = item[filtro];
                            return _valorPropriedade && _valorPropriedade.toString().toLowerCase().indexOf(valor.toString().toLowerCase()) > -1;
                        });
                        Array.prototype.push.apply(retorno, _filtrados);
                    });
                    return retorno;
                };
                function filtrarListaPorFiltros(vetor, filtros) {
                    var retorno = []
                    if (filtros && filtros.length > 0) {
                        angular.element(filtros).each(function (iF, filtro) {
                            var _filtrados = lodash.filter(vetor, function (item) {
                                var _valorPropriedade = item[filtro.propriedade];
                                if (angular.isArray(_valorPropriedade)) {
                                    return _valorPropriedade.indexOf(filtro.valor) > -1;
                                }
                                else {
                                    return _valorPropriedade && _valorPropriedade == filtro.valor;
                                }
                            });
                            Array.prototype.push.apply(retorno, _filtrados);
                        });
                    }
                    else {
                        retorno = vetor;
                    }
                    return retorno;
                };

            }
        }

    }


})
();

(function () {
    'use strict';

    angular.module('dctv-anuncio-carro.directive', [])
        .directive('dctvAnuncioCarro', dctvAnuncioCarro);

    /** @ngInject */
    function dctvAnuncioCarro(lodash, $filter) {

        return {
            restrict: 'E',
            replace: true,
            scope: {
                fonte: '=',
                moeda: '=',
            },
            controllerAs: 'controller',
            templateUrl: 'app/common/directive/dctv-anuncio-carro/dctv-anuncio-carro.tpl.html',
            link: function (scope, element, attrs) {

                scope.obterTituloAnuncio = obterTituloAnuncio;
                scope.obterDescricaoAnuncio = obterDescricaoAnuncio;
                scope.obterDetalhes = obterDetalhes;
                scope.obterPreco = obterPreco;
                scope.obterCodigoTaxa = obterCodigoTaxa;
                scope.obterNumeroParcelas = obterNumeroParcelas;
                scope.obterCaracteristicasFisicas = obterCaracteristicasFisicas;
                scope.obterCaracteristicas = obterCaracteristicas;
                scope.obterIconeCaracteristicaFisica = obterIconeCaracteristicaFisica;
                scope.obterTituloCaracteristica = obterTituloCaracteristica;
                scope.obterValorCaracteristica = obterValorCaracteristica;
                scope.obterDesconto = obterDesconto;
                scope.obterPrecoDesconto = obterPrecoDesconto;
                scope.obterImagem = obterImagem;

                var _verDetalhes = false;

                function obterMoeda() {
                    return scope.moeda;
                }

                function obterDetalhes() {
                    return scope.fonte.detalhes;
                }

                function obterTituloAnuncio() {
                    return scope.fonte.tituloAnuncio;
                };

                function obterCaracteristicasFisicas() {
                    return scope.fonte.caracteristicasFisicas;
                };

                function obterCaracteristicas() {
                    return scope.fonte.caracteristicas;
                };

                function obterDescricaoAnuncio() {
                    return scope.fonte.descricao;
                };

                function obterImagem() {
                    return scope.fonte.imagem;
                }

                function obterPreco(retornarValor) {
                    if (retornarValor) {
                        return scope.fonte.preco;
                    }
                    else {
                        if (obterMoeda()) {
                            return $filter('currency')(scope.fonte.preco * obterMoeda().valor, obterMoeda().simbolo + ' ', 2);
                        } else {
                            return $filter('currency')(scope.fonte.preco, 'R$', 2);
                        }
                    }
                };

                function obterCodigoTaxa() {
                    return scope.fonte.codigoTaxa;
                };

                function obterNumeroParcelas() {
                    return scope.fonte.detalhesPagamento.numeroParcelas + 'x';
                };

                function obterTituloCaracteristica(caracteristica) {
                    return caracteristica.titulo;
                };

                function obterValorCaracteristica(caracteristica) {
                    if (caracteristica.valor === true || caracteristica.valor === false) {
                        return '';
                    }
                    else {
                        return caracteristica.valor;
                    }
                };

                function obterDesconto() {
                    return scope.fonte.detalhesPagamento.desconto;
                };

                function obterPrecoDesconto() {
                    var precoDesconto = obterPreco(true) - (parseFloat('0.' + scope.fonte.detalhesPagamento.desconto) * obterPreco(true));
                    if (obterMoeda()) {
                        return $filter('currency')(precoDesconto * obterMoeda().valor, obterMoeda().simbolo + ' ', 2);
                    } else {
                        return $filter('currency')(precoDesconto, 'R$', 2);
                    }
                }

                scope.alternarDetalhes = alternarDetalhes;
                scope.eDetalhesVisivel = eDetalhesVisivel;
                function alternarDetalhes() {
                    _verDetalhes = !_verDetalhes;
                }

                function eDetalhesVisivel() {
                    return !_verDetalhes;
                }

                function obterIconeCaracteristicaFisica(caracteristica) {

                    var _imagens = [
                        {titulo: 'Portas', classe: 'portas'},
                        {titulo: 'Pessoas', classe: 'passageiro'},
                        {titulo: 'Malas', classe: 'bagagem'},
                        {titulo: 'Ar Condicionado', classe: 'ar-condicionado'},
                        {titulo: 'Câmbio Automático', classe: 'cambio-automatico'},
                        {titulo: 'Câmbio Manual', classe: 'cambio-manual'},
                    ];

                    return lodash.find(_imagens, function (imagem) {
                        return imagem.titulo == caracteristica.titulo || imagem.titulo == caracteristica.titulo + ' ' + caracteristica.valor;
                    }).classe;
                };

            }
        }

    }


})
();

